import moment from 'moment'
import 'moment/locale/ru';
import ScheduleCard from './ScheduleCard';
import { useState } from 'react'
import Popup from '../Popup';
import StudentCard from '../student/StudentCard';
const startOfWeek = moment().startOf('isoWeek');
const endOfWeek = moment().endOf('isoWeek');

const days = [];
let day = startOfWeek;

while (day <= endOfWeek) {
    days.push(day.toDate());
    day = day.clone().add(1, 'd');
}

function getWeek(date) {
    const startOfWeek = moment(date).startOf('isoWeek');
    const endOfWeek = moment(date).endOf('isoWeek');

    const days = [];
    let day = startOfWeek;

    while (day <= endOfWeek) {
        days.push(day.toDate());
        day = day.clone().add(1, 'd');
    }

    return days;
}


function Schedule() {
    moment.locale('ru')
    const [day, setDays] = useState(days);
    const [currentWeekMove, setWeekMove] = useState(0);
    const [modalState, changeModalState] = useState({
        transferModal: false,
        addStudentModal: false,
        cancelModal: false
    })
    const [student, changeStudent] = useState({})
    const [students, addStudent] = useState([])
    return (
        <div className="schedule">
            <h1 className="schedule__title"></h1>
            <div className="d-flex justify-content-between">
                <button className="navigation-btn navigation-btn--prev " onClick={() => {
                    setDays(getWeek(moment().day(currentWeekMove - 7)))
                    setWeekMove(currentWeekMove - 7)
                }
                }>Prev</button>
                <button className="navigation-btn navigation-btn--next " onClick={() => {
                    setDays(getWeek(moment().day(currentWeekMove + 7)))
                    setWeekMove(currentWeekMove + 7)
                }
                }>Next</button>
            </div>
            <div className="grid-4 mt-4 mb-4">
                {day.map((day, index) => {
                    const currentLang = moment(day).lang("ru").format('dddd');

                    let weekend = [6, 0]
                    return (
                        <ScheduleCard key={index} clickHandler={() => {
                            if (moment(day) > moment()) {
                            changeModalState({ ...modalState, addStudentModal: true })

                            changeStudent({ ...student, date: moment(day).format('DD-MM-YYYY HH:mm:ss') })
                            }
                        
                        }}
                        deleteHandler={() => {
                            changeModalState({ ...modalState, cancelModal: true })
                        }}
                        transferHandler={() => {
                            changeModalState({ ...modalState, transferModal: true })
                        }} isWeekend={weekend.includes(moment(day).day())} students={students.filter(stud => moment(stud.date, 'DD-MM-YYYY HH:mm:ss').format('DD-MM-YYYY') === moment(day).format('DD-MM-YYYY'))} name={currentLang.charAt(0).toUpperCase() + currentLang.slice(1)} date={moment(day).format('DD-MM-YYYY')} />
                    )
                })}
            </div>

            <div className="card">
                <div className="card__header">
                    <h3 className="card__title"> </h3>
                </div>
                <div className="pl-3 pr-3">
                    <table className="w-100 mt-4 mb-4 schedule__table">
                        <thead>
                            <tr>
                                <td></td>
                                <td> </td>
                                <td> </td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td> </td>
                                <td>23.05.2021</td>
                                <td>13.06.2021</td>
                            </tr>
                            <tr>
                                <td> </td>
                                <td>23.05.2021</td>
                                <td>13.06.2021</td>
                            </tr>
                            <tr>
                                <td> </td>
                                <td>23.05.2021</td>
                                <td>13.06.2021</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <Popup isSeen={modalState.addStudentModal}>
                <div className="d-flex flex-column align-items-start">
                    <div className="d-flex w-100 align-items-start justify-content-between mb-4">
                        <h2 className="material__title"> </h2>
                        <button onClick={() => {
                            changeModalState(
                                { ...modalState, addStudentModal: false }
                            )
                        }} type="button" className="btn-close align-self-end text-dark" aria-label="Close">
                        </button>

                    </div>
                    <h3 className="card__title"> </h3>
                    <div className="cards grid-2 mb-3 w-100">
                        <input onChange={() => { changeStudent({ ...student, title: ' ' }) }} id="student1" value="1" name="student" className="d-none" type="radio" />
                        <label htmlFor="student1">
                            <StudentCard card={
                                {
                                    id: 1,
                                    title: ' ',
                                    image: 'student3.png'
                                }
                            } />
                        </label>

                        <input onChange={() => { changeStudent({ ...student, title: ' ' }) }} value="2" id="student2" name="student" className="d-none" type="radio" />
                        <label className="" htmlFor="student2">
                            <StudentCard card={
                                {
                                    id: 1,
                                    title: ' ',
                                    image: 'student3.png'
                                }
                            } />
                        </label>
                    </div>
                    <h3 className="card__title">   </h3>
                    <input onChange={(evt) => {
                        const time = evt.target.value.split(':');

                    
                        changeStudent({ ...student, date: moment(student.date, 'DD-MM-YYYY HH:mm:ss').set({ hour: Number(time[0]), minute: Number(time[1]) }).format('DD-MM-YYYY HH:mm:ss') })
                    }} className="w-100 input" type="time" />
                    <h3 className="card__title mt-4">   (.)</h3>
                    <input value={student.duration} onChange={(evt) => {
                        changeStudent({ ...student, duration: evt.target.value })
                    }} className="w-100 input" type="number" />
                    <button onClick={() => {
                        changeModalState(
                            { ...modalState, addStudentModal: false }
                        )
                        addStudent([...students, student])

                    }} className="add-button-blue mt-3 pl-5 pr-5 pt-3 pb-3"></button>
                </div>
            </Popup>
            <Popup isSeen={modalState.transferModal}>
                <div className="d-flex flex-column">
                    <div className="d-flex w-100 align-items-start justify-content-between mb-4">
                        <h2 className="material__title"> </h2>
                        <button onClick={() => {
                            changeModalState(
                                { ...modalState, transferModal: false }
                            )
                        }} type="button" className="btn-close align-self-end text-dark" aria-label="Close">
                        </button>

                    </div>
                    <label className="card__title">  </label>
                    <input className="input w-100" type="date" />
                    <label className="card__title">   </label>
                    <input className="input w-100" type="time" />
                    <label className="card__title">   (.)</label>
                    <input className="input w-100" type="number" />
                    <button className="add-button-blue mt-3 pl-5 pr-5 pt-3 pb-3"></button>
                </div>
            </Popup>
            <Popup isSeen={modalState.cancelModal}>
            <button onClick={() => {
                            changeModalState(
                                { ...modalState, cancelModal: false }
                            )
                        }} type="button" className="btn-close align-self-end text-dark" aria-label="Close">
                        </button>
                <p className="card__description"> ,      </p>
                <StudentCard card={{
                                    id: 1,
                                    title: ' ',
                                    image: 'student3.png'
                                }}/>
                <p> </p>  
                <button onClick={() => {
                    addStudent([])
                    changeModalState(
                        { ...modalState, cancelModal: false }
                    )
                }} className="add-button-blue mt-3 pl-5 pr-5 pt-3 pb-3"></button>   
                <button onClick={() => {
                      changeModalState(
                        { ...modalState, cancelModal: false }
                    )
                }} className="btn w-100 btn-cancel mt-3 pl-5 pr-5 pt-3 pb-3 btn-outline-dark"></button>           
            </Popup>
        </div>
    )
}

export default Schedule;